<template>
  <div id="operation">
    <el-row>
      <el-col :span="4">
    <el-tooltip class="item" effect="dark" content="回退" placement="top-end">
      <div id="opt-item" class="el-icon-arrow-left" onMouseDown="this.style.color='#409eff'; this.style.border='1px solid #409eff'" onMouseUp="this.style.color='black'; this.style.border='none'"></div>
    </el-tooltip>
    <el-tooltip class="item" effect="dark" content="前进" placement="top-end">
      <div id="opt-item" class="el-icon-arrow-right" onMouseDown="this.style.color='#409eff'; this.style.border='1px solid #409eff'" onMouseUp="this.style.color='black'; this.style.border='none'"></div>
    </el-tooltip>
    <el-tooltip class="item" effect="dark" content="添加" placement="top-end">
      <div id="opt-item" class="el-icon-plus" @click="handlePlus" onMouseDown="this.style.color='#409eff'; this.style.border='1px solid #409eff'" onMouseUp="this.style.color='black'; this.style.border='none'"></div>
    </el-tooltip>
    <el-tooltip class="item" effect="dark" content="修改" placement="top-end">
      <div id="opt-item" class="el-icon-edit" @click="handleEdit" onMouseDown="this.style.color='#409eff'; this.style.border='1px solid #409eff'" onMouseUp="this.style.color='black'; this.style.border='none'"></div>
    </el-tooltip>
    <el-tooltip class="item" effect="dark" content="删除" placement="top-end">
      <div id="opt-item" class="el-icon-delete" @click="handleDelete" onMouseDown="this.style.color='#409eff'; this.style.border='1px solid #409eff'" onMouseUp="this.style.color='black'; this.style.border='none'"></div>
    </el-tooltip>
    <el-tooltip class="item" effect="dark" content="对账单" placement="top-end">
      <div id="opt-item" class="el-icon-tickets" @click="handleDownloadExecel" onMouseDown="this.style.color='#409eff'; this.style.border='1px solid #409eff'" onMouseUp="this.style.color='black'; this.style.border='none'"></div>
    </el-tooltip>
    <el-tooltip class="item" effect="dark" content="收款" placement="top-end">
      <div id="opt-item" class="el-icon-sold-out" @click="handleBill" onMouseDown="this.style.color='#409eff'; this.style.border='1px solid #409eff'" onMouseUp="this.style.color='black'; this.style.border='none'"></div>
    </el-tooltip>
    </el-col>
      <el-col :span="8">
        <el-radio v-model="selectOwner" label="企业" size="mini" border="true" style="top:-2px" @change="holderText='请选择企业客户'">企业</el-radio>
        <el-select size="mini" style="top:-2px" :placeholder="holderText" v-model="ownerValue">
          <el-option v-for="item in OwnerOptions" :key="item.value" :label="item.value" :value="item.value">
          </el-option>
        </el-select>
        <el-radio v-model="selectOwner" label="销售员" size="mini" border="true" style="top:-2px" @change="holderText='请选择销售人员'">销售员</el-radio>
      </el-col>
      <el-col :span="10" pull="1">
        <el-date-picker size="mini" style="top: -2px" v-model="startDate" placeholder="开始日期"></el-date-picker>
        <strong style="font-size:15px">至</strong>
        <el-date-picker size="mini" style="top: -2px" v-model="endDate" placeholder="结束日期"></el-date-picker>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data () {
    return {
      selectOwner: '企业',
      holderText: '请选择企业客户',
      startDate: '',
      endDate: '',
      OwnerOptions: [{value: '青海康普'}, {value: '小米科技'}],
      ownerValue: '',
    }
  },
  methods: {
    handlePlus () {
      this.$emit('Message', 'PLUS')
    },
    handleEdit () {
      this.$emit('Message', 'EDIT')
    },
    handleDelete () {
      this.$emit('Message', 'DELETE')
    },
    handleDownloadExecel () {
      this.$emit('Message', 'EXECEL')
    },
    handleBill () {
      this.$emit('Message', 'BILL')
    }
  }
}
</script>

<style scoped>
 #operation {
   font-size: 22px;
   box-sizing: border-box;
   background-color: #b5d0e5
 }
 #opt-item {
   margin-top: 5px;
   text-align: center;
   cursor: pointer;
 }
</style>



