<template>
  <div style="height:100%">
    <el-dialog  id="productDialog" title="产品管理" :visible.sync="productVisible" @close="handleClose">
      <el-form :model="ProductInfo" label-width="100px">
        <el-row>
          <el-col span="12">
            <el-form-item label="产品名称：">
              <el-input v-model="ProductInfo.proname"></el-input>
            </el-form-item>
          </el-col>
          <el-col span="12">
            <el-form-item label="产品分类：">
              <el-input v-model="ProductInfo.category"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="产品描述：">
              <el-input  type="textarea" placeholder="请输入产品描述" v-model="ProductInfo.description"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row style="text-align:center">
          <el-button @click="maintProInfo(maint_opt)">保存</el-button>
          <el-button>取消</el-button>
        </el-row>
      </el-form>
    </el-dialog>
    <basic-button @Message="listenButton"></basic-button>
    <el-table  :data="ProductTable" border="true" ref="ProductTable"  @selection-change="changeSelection" max-height="auto" height="auto">
      <el-table-column type="selection" width="50" align="center">
      </el-table-column>
      <el-table-column type="index" width="50" align="center" label="序号" :index="handleIndex">
      </el-table-column>
      <el-table-column prop="proname" align="center" width="300" label="产品名称">
      </el-table-column>
      <el-table-column  prop="category" align="center" width="200" label="产品类别">
      </el-table-column>
      <el-table-column  prop="description" align="center" label="产品描述">
      </el-table-column>
    </el-table>
    <el-pagination background :page-size="pageSize" @size-change="changeSize" @current-change="changeCurrentPage" layout="prev, pager, next" :total="total" :current-page="currentPage">
    </el-pagination>
  </div>
</template>
<script>
import { param } from '../serial.js'
import BasicButton from './BasicButton'
export default {
  name: 'ProductInfo',
  components: {
    BasicButton
  },
  data () {
    return {
      currentPage: 1,
      pageSize: 10,
      total: 0,
      currentKey: '',
      maint_opt: 'SAVE',
      productVisible: false,
      ProductInfo: {
        proid: 0,
        proname: '',
        category: '',
        description: ''
      },
      DBRequest: {
        command: '',
        filter: {
          size: '',
          offset: '',
        },
        message: {}
      },
      ProductTable: [],
      ProductNameList: [],
      ItemSelected: [],
      Result: 'NONE' // request executed resut: NONE, SUCCESS, ERROR, FAILED
    }
  },
  mounted () {
    this.clearInfo(this.ProductInfo)
    this.loadData(0, this.pageSize)
    /*this.ProductTable = [{proname: '镀铝膜', category: '镀膜', description: '这是镀铝膜'},
      {proname: '沙棘珍果膜', category: '镀膜', description: '康普专用'},
      {proname: '沙棘珍果膜', category: '镀膜', description: '康普专用'},
      {proname: '沙棘珍果膜', category: '镀膜', description: '康普专用'}]*/
 },
  methods: {
    checkResult () {
      var res = this.Result
      switch (res) {
        case 'SUCCESS':
          alert('执行成功')
          break
        case 'ERROR':
          alert('执行有错误')
          break
        case 'NONE':
          alert('操作失败，请重新操作')
          break
        case 'FAILED':
        default:
          alert('执行失败')
      }
      this.Result = 'NONE'
    },
    handleClose () {
      this.productVisible = false
      this.changeSelection()
      this.clearInfo(this.ProductInfo)
      this.clearDialog('ProductInfo')
      this.$emit('Message', 'CLOSEWINDOWS')
    },
    maintProInfo (opt) {
      if (opt == 'SAVE') {
        this.DBRequest.command = 'SAVE_PRODUCT_INFO'
        this.DBRequest.message = this.ProductInfo
        this.axios.post('http://203.195.235.18:8080/product-page/', param(this.DBRequest)).then((res) => {
          if (res.data.answercode == 'SUCCESS') {
            this.ProductTable.push({proid:res.data.proid, proname: res.data.answermsg.proname, category: res.data.answermsg.category, description: res.data.answermsg.description})
            this.total = Number(res.data.answercfg.prototal)
            alert('添加成功')
          } else {
            alert('添加失败')
          }
        }).catch((err) => {
          console.log(err)
        })
      } else if (opt == 'EDIT') {
        this.DBRequest.command = 'EDIT_PRODUCT_INFO'
        this.DBRequest.message = this.ProductInfo
        this.axios.post('http://203.195.235.18:8080/product-page/', param(this.DBRequest)).then((res) => {
          if (res.data.answercode == 'SUCCESS') {
          var offset = this.pageSize * (this.currentPage - 1)
          this.loadData(offset, this.pageSize)
            alert('修改成功')
          } else {
            alert('修改失败')
          }
        }).catch((err) => {
          console.log(err)
        })
      } else {
        alert('请重新操作')
      }
    },
    saveProInfo () {
      this.productVisible = true
      this.maint_opt = 'SAVE'
    },
    editProInfo () {
      var len = this.ItemSelected.length
      if (len == 1) {
        var item = this.ItemSelected[0]
        this.ProductInfo.proid = item.proid
        this.ProductInfo.proname = item.proname
        this.ProductInfo.category = item.category
        this.ProductInfo.description = item.description
        this.productVisible = true
        this.maint_opt = 'EDIT'
      } else if (this.ItemSelected == 0) {
        alert('请勾选需要编辑的信息')
      } else {
        alert('每次只能编辑一条信息')
      }
    },
    deleteProInfo () {
      if (this.ItemSelected.length < 1) {
        alert('请选择需要删除的信息')
      } else if (this.ItemSelected.length > 1) {
        alert('请每次删除一行')
      } else {
        this.DBRequest.command = 'DELETE_PRODUCT_INFO'
        this.ProductInfo = this.ItemSelected.pop()
        this.DBRequest.message = this.ProductInfo
        this.axios.post('http://203.195.235.18:8080/product-page/', param(this.DBRequest)).then((res) => {
          if (res.data.answercode == 'SUCCESS') {
            this.total = Number(res.data.answercfg.prototal)
            var offset = this.pageSize * (this.currentPage - 1)
            if (this.total > offset) {
              this.loadData(offset, this.pageSize)
            } else {
              this.loadData(offset-this.pageSize, this.pageSize)
            }
            alert('删除成功')
          } else {
            alert('删除失败')
          }
        }).catch((err) => {
          console.log(err)
        })
      }
    },
    listenButton (msg) {
      switch (msg) {
        case 'PLUS':
          this.saveProInfo()
          break
        case 'EDIT':
          this.editProInfo()
          break
        case 'DELETE':
          this.deleteProInfo()
          break
        default:
          alert('error')
      }
    },
    changeSelection (val) {
      if (val) {
        this.ItemSelected = val
      }else {
        this.$refs.ProductTable.clearSelection()
      }
    },
    changeCurrentPage (val) {
      this.currentPage = val
      var offset = this.pageSize * (val - 1)
      this.loadData(offset, this.pageSize)
    },
    clearDialog (formName) {
      this.$refs[formName].resetFieds()
    },
    clearInfo (object) {
      for (const key in object) {
        if (object.hasOwnProperty(key)) {
          object[key] = ''
        }
      }
    },
    loadData (offset, size) {
      this.DBRequest.command = 'GET_PRODUCT_INFO'
      this.DBRequest.filter.size = size
      this.DBRequest.filter.offset = offset
      this.DBRequest.message = this.ProductInfo
      this.axios.post('http://203.195.235.18:8080/product-page/', param(this.DBRequest)).then((res) => {
        if (res.data.answercode == 'SUCCESS') {
          this.ProductTable = res.data.answermsg
          this.total = Number(res.data.answercfg.prototal)
        } else if (res.data.answercode == 'FAILED') {
          alert(res.data.answermsg)
        } else {
          alert('请重新加载')
        }
      })
    }
  }
}

</script>
<style scoped>
  #tableHeader {
    color: chartreus
  }
  #productDialog {
    height: 100%;
    width: 100%;
  }
  .el-input {
    width: 100%;
  }
  .el-table {
    height: 86% !important;
    color:blue;
    margin-bottom: 10px;
  }
  .el-pagination {
    text-align: center;
  }
</style>



