<template>
  <div style="height:100%">
    <el-dialog title="客户管理" :visible.sync="customerVisible" @close="handleClose">
      <el-form ref="CustomerInfo" :model="CustomerInfo" label-width="100px">
        <el-row>
          <el-col :span="12">
            <el-form-item class="wholeline" label="客户名称：">
              <el-input v-model="CustomerInfo.custname"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item class="dialog-item" label="客户邮编：">
              <el-input v-model="CustomerInfo.postcode"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item class="dialog-item" label="联系姓名：">
              <el-input v-model="CustomerInfo.contacter"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item class="dialog-item" label="公司职位：">
              <el-input v-model="CustomerInfo.position"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
          <el-form-item class="dialog-item" label="联系电话：">
            <el-input v-model="CustomerInfo.telephone"></el-input>
          </el-form-item>
          </el-col>
          <el-col :span="12">
          <el-form-item class="dialog-item" label="联系手机：">
            <el-input v-model="CustomerInfo.cellphone"></el-input>
          </el-form-item>
          </el-col>
        </el-row>
        <el-form-item class="dialog-item" label="客户地址：">
          <el-input v-model="CustomerInfo.address"></el-input>
        </el-form-item>
        <el-form-item class="dialog-item" label="客户备注：">
          <el-input type="textarea" v-model="CustomerInfo.comment"></el-input>
        </el-form-item>
        <el-row class="dialog-item" style="text-align:center">
          <el-col :span="12">
            <el-button @click="maintCustInfo(maint_opt)">确定</el-button>
          </el-col>
          <el-col :span="12">
            <el-button @click="cancelCustInfo">取消</el-button>
          </el-col>
        </el-row>
      </el-form>
    </el-dialog>
    <basic-button @Message="listenButton"></basic-button>
    <el-table :data="CustomerTable" style="width:100%" @selection-change="changeSelection" ref="CustomerTable" max-height="auto" height="auto" border>
      <el-table-column type="selection" width="30" align="center">
      </el-table-column>
      <el-table-column type="index" label="序号" :index="handleIndex" width="60" align="center">
      </el-table-column>
      <el-table-column prop="custname" label="客户名称" width="200" align="center">
      </el-table-column>
      <el-table-column prop="contacter" label="联系人姓名" width="100" align="center">
      </el-table-column>
      <el-table-column prop="position" label="联系人职位" width="100" align="center">
      </el-table-column>
      <el-table-column prop="telephone" label="联系电话" width="120" align="center">
      </el-table-column>
      <el-table-column prop="cellphone" label="移动电话" width="120" align="center">
      </el-table-column>
      <el-table-column prop="address" label="客户地址" width="380" align="center">
      </el-table-column>
      <el-table-column prop="postcode" label="客户邮编" width="100" align="center">
      </el-table-column>
      <el-table-column prop="comment" label="客户备注" align="center" >
      </el-table-column>
    </el-table>
    <div style="text-align:center">
      <el-pagination background @size-change="changeSize" @current-change="changeCurrentPage" layout="prev, pager, next" :total="total" :current-page="currentPage" :page-size="pageSize">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import { param } from '../serial.js'
import BasicButton from './BasicButton'
export default {
  name: 'CustInfo',
  components: {
    BasicButton
  },
  data () {
    return {
      customerVisible: false,
      currentPage: 1,
      pageSize: 10,
      total: 0,
      rateValue: '',
      maint_opt: 'SAVE',
      CustomerInfo: {
        custid: '',
        custname: '',
        contacter: '',
        position: '',
        telephone: '',
        cellphone: '',
        address: '',
        postcode: '',
        comment: ''
      },
      CustomerTable: [],
      ItemSelected: [],
      DBRequest: {
        command: '',
        filter: {
          size: '',
          offset: ''
        },
        message: {}
      }
    }
  },
  mounted () {
    this.clearInfo()
    this.loadData(0, this.pageSize)
  },
  methods: {
    handleClose () {
      this.customerVisible = false
      this.clearDialog('CustomerInfo')
      this.ItemSelected = []
      this.$emit('Message', 'CLOSEWINDOWS')
    },
    clearDialog (formName) {
      this.$refs[formName].resetFieds()
    },
    clearInfo (object) {
      for (const key in object) {
        if (object.hasOwnProperty(key)) {
          object[key] = ''
        }
      }
    },
    cancelCustInfo () {
      this.clearDialog('CustomerInfo')
    },
    saveCustInfo () {
      this.customerVisible = true
      this.maint_opt = 'SAVE'
    },
    editCustInfo () {
      if (this.ItemSelected.length == 1) {
        var item = this.ItemSelected.pop()
        this.CustomerInfo.custname = item.custname
        this.CustomerInfo.contacter = item.contacter
        this.CustomerInfo.position = item.position
        this.CustomerInfo.telephone = item.telephone
        this.CustomerInfo.cellphone = item.cellphone
        this.CustomerInfo.address = item.address
        this.CustomerInfo.postcode = item.postcode
        this.CustomerInfo.comment = item.comment
        this.customerVisible = true
        this.maint_opt = 'EDIT'
      } else if (this.ItemSelected == 0) {
        alert('请勾选需要编辑的信息')
      } else {
        alert('每次只能编辑一条信息')
      }
    },
    deleteCustInfo () {
      if (this.ItemSelected.length < 1) {
        alert('请选择需要删除的信息')
      } else if (this.ItemSelected.length > 1) {
        alert('请每次删除一行')
      } else {
        this.DBRequest.command = 'DELETE_CUST_INFO'
        this.CustomerInfo = this.ItemSelected.pop()
        this.DBRequest.message = this.CustomerInfo
        this.axios.post('http://203.195.235.18:8080/cust-page/', param(this.DBRequest)).then((res) => {
          if (res.data.answercode == 'SUCCESS') {
            this.total = Number(res.data.answercfg.custtotal)
            var offset = this.pageSize * (this.currentPage - 1)
            if (this.total > offset) {
              this.loadData(offset, this.pageSize)
            } else {
              this.loadData(offset-this.pageSize, this.pageSize)
            }
            alert('删除成功')
          } else if (res.data.answercode == 'FAILED') {
            alert(res.data.answermsg)
          } else {
            alert('发生错误，请刷新页面')
          }
        }).catch((err) => {
          console.log(err)
        })
      }
    },
    maintCustInfo (opt) {
      if (opt == 'SAVE') {
        this.DBRequest.command = 'SAVE_CUSTOMER_INFO'
        this.DBRequest.message = this.CustomerInfo
        this.axios.post('http://203.195.235.18:8080/cust-page/', param(this.DBRequest)).then((res) => {
        if (res.data.answercode == 'SUCCESS') {
          this.CustomerTable.push({custname: res.data.answermsg.custname, contacter: res.data.answermsg.contacter, position: res.data.answermsg.position,
                                   telephone: res.data.answermsg.telephone, cellphone: res.data.answermsg.cellphone, postcode: res.data.answermsg.postcode, address: res.data.answermsg.address, comment: res.data.answermsg.comment})
          this.total = Number(res.data.answercfg.custtotal)
          alert('添加成功')
        } else if (res.data.answercode == 'FAILED') {
          alert('添加失败')
        } else {
          alert('操作失败')
        }
        })
      } else if (opt == 'EDIT') {
        this.DBRequest.command = 'EDIT_CUST_INFO'
        this.DBRequest.message = this.CustomerInfo
        this.axios.post('http://203.195.235.18:8080/cust-page/', param(this.DBRequest)).then((res) => {
          if (res.data.answercode == 'SUCCESS') {
          var offset = this.pageSize * (this.currentPage - 1)
          this.loadData(offset, this.pageSize)
            alert('修改成功')
          } else {
            alert('修改失败')
          }
        }).catch((err) => {
          console.log(err)
        })
      } else {
        alert('请重新操作')
      }
    },
    changeSelection (val) {
      if (val) {
        this.ItemSelected = val
      }else {
        this.$refs.CustomerTable.clearSelection()
      }
    },
    changeCurrentPage (val) {
      this.currentPage = val
      var offset = this.pageSize * (val - 1)
      this.loadData(offset, this.pageSize)
    },
    listenButton (msg) {
      switch (msg) {
        case 'PLUS':
          this.saveCustInfo()
          break
        case 'EDIT':
          this.editCustInfo()
          break
        case 'DELETE':
          this.deleteCustInfo()
          break
        default:
          alert('error')
      }
    },
    loadData (offset, size) {
      this.DBRequest.command = 'GET_CUSTOMER_INFO'
      this.DBRequest.filter.size = size
      this.DBRequest.filter.offset = offset
      this.DBRequest.message = this.CustomerInfo
      this.axios.post('http://203.195.235.18:8080/cust-page/', param(this.DBRequest)).then((res) => {
        if (res.data.answercode == 'SUCCESS') {
          this.CustomerTable = res.data.answermsg
          this.total = Number(res.data.answercfg.custtotal)
        } else if (res.data.answercode == 'FAILED') {
          alert(res.data.answermsg)
        } else {
          alert('发生错误，请刷新页面')
        }
      })
    }
  }
}
</script>
<style scoped>
.el-form-item {
  width: 30%;
  padding: 0px;
  margin: 0px;
  color: blue;
}
.el-form-item.wholeline {
  width: 100%;
  color:brown;
}
.contact {
  font-weight: bold;
}
.el-table {
    height: 86% !important;
    color:blue;
    margin-bottom: 10px;
}
.dialog-item {
  width: 100%;
  margin-top: 6px;
}
</style>



