<template>
  <div id="operation">
    <el-tooltip class="item" effect="dark" content="回退" placement="top-end">
      <div id="opt-item" class="el-icon-arrow-left" onMouseDown="this.style.color='#409eff'; this.style.border='1px solid #409eff'" onMouseUp="this.style.color='black'; this.style.border='none'"></div>
    </el-tooltip>
    <el-tooltip class="item" effect="dark" content="前进" placement="top-end">
      <div id="opt-item" class="el-icon-arrow-right" onMouseDown="this.style.color='#409eff'; this.style.border='1px solid #409eff'" onMouseUp="this.style.color='black'; this.style.border='none'"></div>
    </el-tooltip>
    <el-tooltip class="item" effect="dark" content="添加" placement="top-end">
      <div id="opt-item" class="el-icon-plus" @click="handlePlus" onMouseDown="this.style.color='#409eff'; this.style.border='1px solid #409eff'" onMouseUp="this.style.color='black'; this.style.border='none'"></div>
    </el-tooltip>
    <el-tooltip class="item" effect="dark" content="修改" placement="top-end">
      <div id="opt-item" class="el-icon-edit" @click="handleEdit" onMouseDown="this.style.color='#409eff'; this.style.border='1px solid #409eff'" onMouseUp="this.style.color='black'; this.style.border='none'"></div>
    </el-tooltip>
    <el-tooltip class="item" effect="dark" content="删除" placement="top-end">
      <div id="opt-item" class="el-icon-delete" @click="handleDelete" onMouseDown="this.style.color='#409eff'; this.style.border='1px solid #409eff'" onMouseUp="this.style.color='black'; this.style.border='none'"></div>
    </el-tooltip>
  </div>
</template>
<script>
export default {
  data () {
    return {
    }
  },
  methods: {
    handlePlus () {
      this.$emit('Message', 'PLUS')
    },
    handleEdit () {
      this.$emit('Message', 'EDIT')
    },
    handleDelete () {
      this.$emit('Message', 'DELETE')
    }
  }
}
</script>

<style scoped>
 #operation {
   background-color: #b5d0e5;
   font-size: 22px;
   box-sizing: border-box;

 }
 #opt-item {
   margin-top: 5px;
   text-align: center;
   cursor: pointer;
 }
</style>



